@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);


.header{
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 12px;
}

.header-logo{
    height: 2.7rem;
    max-width: 140px;
    margin-right: 24px;
}

.header-right{
    display: flex;
    flex: 1 1;
    justify-content: space-between;
    align-items: center;
    height: 72px;
}

.header-location-search-container{
    height: 54px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    border: 1px solid var(--border-white);
    box-shadow: var(--primary-box-shadow);
    width: 70%;
    background-color: var(--primary-bg);

}

.location-wrapper{
    display: flex;
    flex: 1 1;
    justify-content: space-between;
    padding: 0 10px;
}

.location-icon-name{
    display: flex;
    color: var(--zomato-grey);
}
.location-icon{
    color: var(--zomato-red);
    font-size: 20px;
    margin-right: 8px;
}

.location-search-seperator{
    height: 20px;
    border: 0.5px solid var(--gray);
}

.header-SearchBar{
    display: flex;
    flex: 2 1;
}

.search-icon{
    font-size: 20px;
    color: var(--zomato-grey);
    margin: 0 12px;
}

.search-input{
    border: none;
    outline: none;
    width: 60%;
    font-size: 14px;
}

.profile-wrapper{
    display: flex;
    justify-content: space-between;
    width: 15%;
    align-items: center;
}

.header-profile-img{
    height: 40px;
    width: 40px;
    border-radius: 50%;
}

.header-username{
    font-size: 16px;
    font-weight: 500;
}

.profile-options-icon{
    font-size: 20px;
}
.tab-options{
    margin-top: 8px;
    box-shadow: var(--border-white) 0px -0.5px 0px inset;
}

.option-wrapper{
    display: flex;
}

.tab-item{
    margin-right: 24px;
    padding: 12px 12px 20px 12px;
}

.tab-name{
    font-size: 18px;
    letter-spacing: 1.2px;
    font-weight: 600;
    margin-left: 10px;
    color: var(--inactive-text);
}

.tab_image-container{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    background-color: var(--inactive-color);
}

.tab-image{
    height: 30px;
    width: 30px;
}

.active-tab{
    border-bottom: 2px solid var(--zomato-red);
}

.active-tab .tab-name{
    color: var(--zomato-red);
}
.filter-item{
    padding: 8px;
    display: flex;
    background-color: var(--primary-bg);
    color: var(--filter-item);
    border: 1px solid var(--gray);
    border-radius: 8px;
    margin: 10px 10px 10px 0;
    font-size: 14px;
}

.filter-name{
    margin-left: 5px;
}
.filters{
    padding: 16px 0;
    display: flex;
    flex-wrap: wrap;
}

.delivery-collection{
    background-color: var(--inactive-color);
    width: 100%;
    padding: 40px 0;
}
.delivery-item-cover{
    height: 190px;
    width: 260px;
    background-color: var(--primary-bg); 
    box-shadow: var(--collection-shadow);
    border-radius: 8px;
}

.delivery-item-image{
    object-fit: cover;
    height: 100%;
    width: 100%;
    border-radius: 8px;
}

.delivery-item-title{
    font-size: 20px;
    font-weight: 500;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-top: 6px;
}
.explore-card{
    margin: 12px 0px 24px 0;
    padding: 10px;
    border-radius: 15px;
    border: 1px solid transparent;
}

.explore-card-cover{
    height: 248px;
    width: 328px;
    border-radius: 15px;
    position: relative;
}

.explore-card-image{
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 15px;
}

.delivery-time{
    position: absolute;
    right: 10px;
    bottom: 10px;
    opacity: 0.8;
    background-color: var(--primary-bg);
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
    font-weight: 600;
    font-size: 12px;
    padding: 2px 5px 3px;
    border-radius: 4px;
}

.pro-off{
    position: absolute;
    left: 0;
    bottom: 40px;
    background-color: var(--tag-red);
    color: var(--primary-bg);
    font-size: 14px;
    padding: 0 6px;
    border-radius: 0 2px 2px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;
}

.discount{
    position: absolute;
    left: 0;
    bottom: 14px;
    background-color: var(--tag-blue);
    color: var(--primary-bg);
    font-size: 14px;
    padding: 0 6px;
    border-radius: 0 2px 2px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;
}

.gold-off{
    position: absolute;
    left: 0;
    bottom: 14px;
    background-color: var(--tag-green);
    color: var(--primary-bg);
    font-size: 14px;
    padding: 0 6px;
    border-radius: 0 2px 2px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;
}

.res-row{
    display: flex;
    justify-content: space-between;
    padding: 8px 0 6px;
}

.res-name {
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 18px;
    font-weight: 600;
    color: var(--heading-text);
  }
  
  .res-rating {
    padding: 0px 8px;
    font-size: 13px;
    font-weight: 600;
    color: var(--primary-bg);
    background-color: var(--tag-green);
    border-radius: 6px;
    height: 24px;
  }
  
  .res-rating i {
    font-size: 10px;
    margin-left: 2px;
  }
  
  .res-cuisine {
    max-width: 56%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 14px;
  }
  
  .res-cuisine-tag {
    margin-right: 4px;
    color: var(--inactive-text);
  }
  
  .res-price {
    font-size: 14px;
    color: var(--inactive-text);
  }
  
  .explore-bottom {
    display: flex;
    align-items: center;
    max-width: 100%;
  }
  
  .res-bottom-text {
    font-size: 12px;
    color: var(--inactive-text);
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-left: 8px;
  }
  
  .card-separator {
    height: 1px;
    margin: 10px 0px 5px;
    background-color: var(--border-white);
  }
  
  .explore-card:hover {
    background-color: var(--primary-bg);
    border: 1px solid var(--border-white);
  }
.explore-section{
    margin-top: 16px !important;
}

.explore-grid{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.top-brand{
    padding-top: 16px;
}

.top-brand-cover{
    height: 150px;
    width: 150px;
    border-radius: 10%;
    background-color: var(--primary-bg);
    box-shadow: var(--collection-shadow);
    border: 0.5px solid var(--border-white);
}

.top-brand-image{
    border-radius: inherit;
    height: 100%;
    width: 100%;
    object-fit: cover;
} 
.collection-wrapper{
    background-color: var(--inactive-color);
}

.collection{
    padding: 15px 0 40px;
}

.collection-subtitle-row{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.collection-subtitle-text{
    color: var(--subtitle-text);
    font-size: 18px;
}

.collection-location{
    color: var(--zomato-red);
    display: flex;
}

.collection-cover{
    height: 320px;
    width: 260px;
    margin-right: 20px;
    border-radius: 6px;
    position: relative;
}

.gradient-bg{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    border-radius: 6px;
    background: var(--bg-gradient);
}

.collection-image{
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 6px;
}

.collection-card-title{
    position: absolute;
    left: 16px;
    bottom: 30px;
    font-size: 18px;
    color: var(--primary-bg);
}

.collection-card-subtitle{
    display: flex;
    color: var(--primary-bg);
}


.max-width{
    max-width: 1100px;
    margin: 0px auto;
}

.absolute-center{
    display: flex;
    justify-content: center;
    align-items: center;
}

.c-p{
    cursor: pointer;
}

.collection-title{
    font-size: 30px;
    color: var(--heading-text);
    margin-bottom: 32px;
    font-weight: 600;
}

.slick-next:before,
.slick-prev:before{
    color: var(--gray);
}
#root{
    --primary-box-shadow: rgb(28 28 28/ 8%) 0 2px 8px;
    --border-white: rgb(232 232 232);
    --primary-bg: rgb(255 255 255);
    --zomato-grey: #828282;
    --zomato-red: #FF7E8B;
    --gray: rgb(207 207 207);
    --inactive-text: #696969;
    --inactive-color: #f8f8f8;
    --filter-text: #969696;
    --heading-text: #1c1c1c;
    --collection-shadow: rgb(0 0 0 /8%) 0 3px 12px;
    --tag-red: #d92662;
    --tag-blue: #256fef;
    --tag-green: #267e3e;
    --subtitle-text: #363636;
    --bg-gradient: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.9) 0%,
        rgba(0, 0, 0, 0.05) 0%,
        rgba(0, 0, 0, 0.05) 85%
  );
}
body{
    margin: 0;
    font-family: "Nunito", sans-serif;
    box-sizing: border-box;
}
